h2 {
    font-weight: bold;
    color: rgb(4, 4, 12);
    text-align: center;
}

/*Creating thicker hr line */
hr.line {
    border:  1px solid black;
}

body {
    resize: both;
    overflow: auto;
    height: 100%;
}

  /*Adding in padding on the left for all row classes*/
.row {
    padding-left: 20px;
    padding-right: 20px;
}
  
.card-title,
.card-footer {
    text-align: center;
}
  
  
  
.card-deck {
    margin-bottom: 20%;
    /* opacity: 95%; */
}
  
.card {
    /* opacity: 500%; */
    background-color: rgb(112, 110, 139);
    color: rgb(5, 5, 5);
}

@media screen and (max-width: 400px) {
    body {
      width: auto;
    }
    
    /*Setting wrapper width*/
    .wrapper {
      width: 100%;
    }

    /*Repositioning of the contact form*/
    #contact,
    #index,
    #portfolio {
        padding-right: 15px;
        margin-right: 10%;
        padding-top: 10px;
        padding-bottom: 30%;
        /* margin-bottom: 10%; */
        width: auto;
    }
    
    /*Floating bio right to clear the picture*/
    .bio {
        float: right;
    }

}